import { useEffect, useState } from "react";
import {
  Camera,
  Save,
  Edit,
  User,
  MapPin,
  Shield,
  CheckCircle,
  ArrowLeft,
} from "lucide-react";
import toast from "react-hot-toast";

function getCookie(name) {
  const cookieArr = document.cookie.split(";");

  for (let cookie of cookieArr) {
    cookie = cookie.trim();
    if (cookie.startsWith(name + "=")) {
      return decodeURIComponent(cookie.substring(name.length + 1));
    }
  }

  return null; // Cookie not found
}

export default function UserProfile() {
  const [activeTab, setActiveTab] = useState("personal");
  const [isEditing, setIsEditing] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [showAddressForm, setShowAddressForm] = useState(false);

  // Fix 1: Initialize businessName properly - retrieve directly without JSON.parse
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    businessName: localStorage.getItem("businessName") || "",
  });

  const [addressData, setAddressData] = useState({
    country: "ایران",
    city: "تهران",
    postalCode: "۱۲۳۴۵۶۷۸۹۰",
    streetAddress: "خیابان ولیعصر",
    buildingNumber: "۱۴۵",
    additionalInfo: "طبقه ۳، واحد ۷",
  });

  useEffect(() => {
    fetchUserData();
  }, []);

  const fetchUserData = async () => {
    setIsLoading(true);
    const userId = getCookie("userId");

    if (!userId) {
      toast.error("اطلاعات کاربری یافت نشد");
      setIsLoading(false);
      return;
    }

    try {
      const response = await fetch(
        `http://127.0.0.1:8000/api/user/?id=${userId}`
      );

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const data = await response.json();

      if (!data || data.length === 0) {
        toast.error("اطلاعات کاربری یافت نشد");
        setIsLoading(false);
        return;
      }

      const userData = data[0];

      // Fix 2: Set form data from API response - get businessName directly
      setFormData({
        firstName: userData.first_name || "",
        lastName: userData.last_name || "",
        email: userData.email || "",
        phone: userData.phone || "",
        businessName: localStorage.getItem("businessName") || "",
      });

      // Also fetch address data if needed
      // You would implement this based on your API structure
    } catch (error) {
      toast.error("خطا در دریافت اطلاعات کاربری");
    } finally {
      setIsLoading(false);
    }
  };

  // Fix 3: Remove problematic useEffect that was modifying formData directly
  // This effect was causing the businessName to become "null" when it didn't exist
  // REMOVED:
  /*
useEffect(() => {
  const businessName = JSON.stringify(localStorage.getItem("businessName"));
  if (!businessName) return;
  formData.businessName = businessName;
}, [formData]);
*/

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleAddressChange = (e) => {
    const { name, value } = e.target;
    setAddressData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const saveChanges = async () => {
    setIsLoading(true);
    const userId = getCookie("userId");

    if (!userId) {
      toast.error("اطلاعات کاربری یافت نشد");
      setIsLoading(false);
      return;
    }

    try {

      // First get current user data
      const response = await fetch(
        `http://127.0.0.1:8000/api/user/?id=${userId}`
      );

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(
          `Failed to fetch user data: ${response.status}, Details: ${errorText}`
        );
      }

      const data = await response.json();

      if (!data || data.length === 0) {
        toast.error("اطلاعات کاربری یافت نشد");
        setIsLoading(false);
        return;
      }

      const userData = data[0];

      // Build update object only for changed fields
      const userObj = {};

      if (formData.firstName && formData.firstName !== userData.first_name) {
        userObj.first_name = formData.firstName;
      }

      if (formData.lastName && formData.lastName !== userData.last_name) {
        userObj.last_name = formData.lastName;
      }

      if (formData.email && formData.email !== userData.email) {
        // Basic email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(formData.email)) {
          toast.error("لطفا یک ایمیل معتبر وارد کنید");
          setIsLoading(false);
          return;
        }
        userObj.email = formData.email;
      }

      if (formData.phone && formData.phone !== userData.phone) {
        // Basic phone validation for Iran (optional)
        const phoneRegex = /^(\+98|0)?9\d{9}$/;
        if (!phoneRegex.test(formData.phone)) {
          toast.error("لطفا یک شماره تلفن معتبر وارد کنید");
          setIsLoading(false);
          return;
        }
        userObj.phone = formData.phone;
      }

      // Fix 4: Properly check if business name changed
      const currentBusinessName = localStorage.getItem("businessName") || "";
      const hasBusinessNameChanged =
        formData.businessName !== currentBusinessName;

      // Check if any changes were made
      if (Object.keys(userObj).length === 0 && !hasBusinessNameChanged) {
        toast("هیچ تغییری انجام نشده است");
        setIsEditing(false);
        setIsLoading(false);
        return;
      }

      // Fix 5: Handle business name directly without JSON.parse/stringify
      if (hasBusinessNameChanged) {
        localStorage.setItem("businessName", formData.businessName);

        // If only business name changed, show success and return
        if (Object.keys(userObj).length === 0) {
          toast.success("نام کسب و کار با موفقیت ذخیره شد");
          setIsEditing(false);
          setIsLoading(false);
          return;
        }
      }

      // Continue with API update if other fields changed
      if (Object.keys(userObj).length > 0) {

        const updateUrl = `http://127.0.0.1:8000/api/user/?id=${userId}`;

        // Send PATCH request with changed fields
        const updateResponse = await fetch(updateUrl, {
          method: "PATCH",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify(userObj),
        });

        if (!updateResponse.ok) {
          let errorMessage = `HTTP error! Status: ${updateResponse.status}`;
          try {
            const errorText = await updateResponse.text();
            errorMessage += `, Details: ${errorText}`;
          } catch (e) {
          }
          throw new Error(errorMessage);
        }

        // Handle the response, which might be empty or JSON
        let updateData;
        try {
          updateData = await updateResponse.json();
        } catch (jsonError) {
        }
      }

      toast.success("تغییرات با موفقیت ذخیره شد");
    } catch (error) {
      toast.error("خطا در بروزرسانی اطلاعات");
    } finally {
      setIsLoading(false);
      setIsEditing(false);
    }
  };

  const saveAddress = async () => {
    // Implement address saving logic here based on your API
    toast.success("آدرس با موفقیت ذخیره شد");
    setShowAddressForm(false);
  };

  return (
    <div className="w-full bg-zinc-900 p-6 rounded-lg">
      {/* Profile Header */}
      <ProfileHeader
        formData={formData}
        isEditing={isEditing}
        onIsEditing={setIsEditing}
      />
      {/* Profile Tabs */}
      <ProfileTabs
        activeTab={activeTab}
        setActiveTab={setActiveTab}
        isEditing={isEditing}
        formData={formData}
        handleInputChange={handleInputChange}
        saveChanges={saveChanges}
        showAddressForm={showAddressForm}
        setShowAddressForm={setShowAddressForm}
        addressData={addressData}
        handleAddressChange={handleAddressChange}
        saveAddress={saveAddress}
      />
    </div>
  );
}

function ProfileTabs({
  activeTab,
  setActiveTab,
  isEditing,
  formData,
  handleInputChange,
  saveChanges,
  showAddressForm,
  setShowAddressForm,
  addressData,
  handleAddressChange,
  saveAddress,
}) {
  return (
    <div className="bg-zinc-800 rounded-2xl shadow-lg overflow-hidden transition-all duration-300 hover:shadow-indigo-900/20">
      <div className="border-b border-zinc-700">
        <div className="flex overflow-x-auto px-2">
          <button
            className={`px-5 py-4 font-medium focus:outline-none flex items-center transition-all duration-300 ${
              activeTab === "personal"
                ? "text-indigo-300 border-b-2 border-indigo-500"
                : "text-gray-400 hover:text-indigo-200"
            }`}
            onClick={() => setActiveTab("personal")}
          >
            <User size={18} className="ml-2" />
            <span>اطلاعات شخصی</span>
          </button>
          {/* <button
            className={`px-5 py-4 font-medium focus:outline-none flex items-center transition-all duration-300 ${
              activeTab === "address"
                ? "text-indigo-300 border-b-2 border-indigo-500"
                : "text-gray-400 hover:text-indigo-200"
            }`}
            onClick={() => setActiveTab("address")}
          >
            <MapPin size={18} className="ml-2" />
            <span>آدرس</span>
          </button> */}
        </div>
      </div>

      {/* Personal Info Tab */}
      {activeTab === "personal" && (
        <div className="p-8">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Right Column */}
            <div>
              <div className="mb-6">
                <label
                  className="block text-gray-300 text-sm font-medium mb-2"
                  htmlFor="firstName"
                >
                  نام
                </label>
                <div className="relative">
                  <input
                    className={`border border-gray-600 rounded-xl w-full py-3 px-4 text-gray-200 leading-tight bg-gray-700/70 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-all duration-300 ${
                      !isEditing && "opacity-80"
                    }`}
                    id="firstName"
                    name="firstName"
                    type="text"
                    value={formData.firstName}
                    onChange={handleInputChange}
                    disabled={!isEditing}
                    dir="rtl"
                  />
                  {!isEditing && (
                    <div className="absolute inset-0 bg-transparent cursor-not-allowed"></div>
                  )}
                </div>
              </div>

              <div className="mb-6">
                <label
                  className="block text-gray-300 text-sm font-medium mb-2"
                  htmlFor="lastName"
                >
                  نام خانوادگی
                </label>
                <div className="relative">
                  <input
                    className={`border border-gray-600 rounded-xl w-full py-3 px-4 text-gray-200 leading-tight bg-gray-700/70 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-all duration-300 ${
                      !isEditing && "opacity-80"
                    }`}
                    id="lastName"
                    name="lastName"
                    type="text"
                    value={formData.lastName}
                    onChange={handleInputChange}
                    disabled={!isEditing}
                    dir="rtl"
                  />
                  {!isEditing && (
                    <div className="absolute inset-0 bg-transparent cursor-not-allowed"></div>
                  )}
                </div>
              </div>

              <div className="mb-6">
                <label
                  className="block text-gray-300 text-sm font-medium mb-2"
                  htmlFor="email"
                >
                  آدرس ایمیل
                </label>
                <div className="relative">
                  <input
                    className={`border border-gray-600 rounded-xl w-full py-3 px-4 text-gray-200 leading-tight bg-gray-700/70 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-all duration-300 ${
                      !isEditing && "opacity-80"
                    }`}
                    id="email"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    disabled={!isEditing}
                    dir="ltr"
                  />
                  {!isEditing && (
                    <div className="absolute inset-0 bg-transparent cursor-not-allowed"></div>
                  )}
                </div>
              </div>
            </div>

            {/* Left Column */}
            <div>
              <div className="mb-6">
                <label
                  className="block text-gray-300 text-sm font-medium mb-2"
                  htmlFor="phone"
                >
                  شماره تماس
                </label>
                <div className="relative">
                  <input
                    className={`border border-gray-600 rounded-xl w-full py-3 px-4 text-gray-200 leading-tight bg-gray-700/70 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-all duration-300 ${
                      !isEditing && "opacity-80"
                    }`}
                    id="phone"
                    name="phone"
                    type="text"
                    value={formData.phone}
                    onChange={handleInputChange}
                    disabled={!isEditing}
                    dir="rtl"
                  />
                  {!isEditing && (
                    <div className="absolute inset-0 bg-transparent cursor-not-allowed"></div>
                  )}
                </div>
              </div>

              <div className="mb-6">
                <label
                  className="block text-gray-300 text-sm font-medium mb-2"
                  htmlFor="businessName"
                >
                  نام کسب و کار
                </label>
                <div className="relative">
                  <input
                    className={`border border-gray-600 rounded-xl w-full py-3 px-4 text-gray-200 leading-tight bg-gray-700/70 focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-all duration-300 ${
                      !isEditing && "opacity-80"
                    }`}
                    id="businessName"
                    name="businessName"
                    type="text"
                    value={formData?.businessName}
                    onChange={handleInputChange}
                    disabled={!isEditing}
                  />
                  {!isEditing && (
                    <div className="absolute inset-0 bg-transparent cursor-not-allowed"></div>
                  )}
                </div>
              </div>
            </div>
          </div>

          {isEditing && (
            <div className="mt-8 flex justify-end">
              <button
                onClick={saveChanges}
                className="bg-gradient-to-r from-indigo-600 to-indigo-700 hover:from-indigo-700 hover:to-indigo-800 text-white font-bold py-3 px-8 rounded-xl flex items-center transition-all duration-300 shadow-md hover:shadow-lg shadow-indigo-900/20"
              >
                <span className="ml-2">ذخیره تغییرات</span>
                <Save size={18} />
              </button>
            </div>
          )}
        </div>
      )}

      {/* Address Tab */}
      {/* {activeTab === "address" && (
        <AddressTab
          showAddressForm={showAddressForm}
          setShowAddressForm={setShowAddressForm}
          addressData={addressData}
          handleAddressChange={handleAddressChange}
          saveAddress={saveAddress}
        />
      )} */}
    </div>
  );
}

function ProfileHeader({ formData, isEditing, onIsEditing }) {
  return (
    <div className="bg-zinc-800 rounded-2xl shadow-lg p-8 mb-6 transition-all duration-300 hover:shadow-indigo-900/20">
      <div className="flex flex-col md:flex-row items-center">
        {/* Profile Image */}
        <div className="relative mb-6 md:mb-0 md:ml-8">
          <div className="group relative w-32 h-32 rounded-full overflow-hidden border-4 border-indigo-500 shadow-lg shadow-indigo-900/30">
            <img
              src="/api/placeholder/128/128"
              alt="تصویر پروفایل"
              className="w-full h-full object-cover transition-transform duration-300 group-hover:scale-110"
            />
            <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-40 transition-all duration-300 flex items-center justify-center opacity-0 group-hover:opacity-100">
              <Camera size={24} className="text-white" />
            </div>
          </div>
          <button className="absolute -bottom-1 -right-1 bg-indigo-600 text-white p-2 rounded-full shadow-lg hover:bg-indigo-700 transition-colors">
            <Camera size={16} />
          </button>
        </div>

        {/* Profile Info */}
        <div className="flex-1 text-center md:text-right">
          <h1 className="text-3xl font-bold text-white mb-1">
            {formData?.firstName} {formData?.lastName}
          </h1>
          <p className="text-gray-300 mb-2">{formData?.email}</p>
          <p className="text-sm text-gray-400 mb-3">
            آخرین ورود: ۱۴۰۴/۰۱/۳۰ ساعت ۱۴:۲۵
          </p>

          {formData?.businessName && (
            <div className="flex flex-wrap justify-center md:justify-start gap-3">
              <span className="px-4 py-1 bg-gradient-to-r from-indigo-800 to-indigo-900 text-indigo-100 rounded-full text-sm font-medium shadow-inner shadow-indigo-700/30">
                {formData?.businessName}
              </span>
            </div>
          )}
        </div>

        {/* Edit Button */}
        <div className="mt-6 md:mt-0 md:mr-6">
          <button
            onClick={() => onIsEditing(!isEditing)}
            className={`px-5 py-2 rounded-lg flex items-center transition-all duration-300 ${
              isEditing
                ? "bg-zinc-700 text-gray-200 hover:bg-zinc-600"
                : "bg-gradient-to-r from-indigo-600 to-indigo-700 text-white hover:from-indigo-700 hover:to-indigo-800 shadow-md hover:shadow-lg shadow-indigo-900/20"
            }`}
          >
            <span className="ml-2">
              {isEditing ? "در حال ویرایش" : "ویرایش پروفایل"}
            </span>
            <Edit size={18} />
          </button>
        </div>
      </div>
    </div>
  );
}

